// src/component/TiptapEditor.tsx
import React, { useEffect, useState } from 'react';
import { useEditor, EditorContent } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Image from '@tiptap/extension-image';
import { uploadImage } from '../api/blogService/blog';
import { resizeImage, blobToFile } from '../api/ImageResizer';

// 커스텀 이미지 확장 - 리사이징 가능하도록
const CustomImage = Image.extend({
  addAttributes() {
    return {
      ...this.parent?.(),
      width: {
        default: null,
        renderHTML: attributes => {
          if (!attributes.width) return {};
          return { width: attributes.width };
        },
      },
      height: {
        default: null,
        renderHTML: attributes => {
          if (!attributes.height) return {};
          return { height: attributes.height };
        },
      },
    };
  },
});

interface TiptapEditorProps {
  content: string;
  onChange: (content: string) => void;
}

const TiptapEditor: React.FC<TiptapEditorProps> = ({ content, onChange }) => {
  const [isImageUploading, setIsImageUploading] = useState(false);
  const [selectedImageUrl, setSelectedImageUrl] = useState<string | null>(null);
  const [imageWidth, setImageWidth] = useState<number>(500);

  const editor = useEditor({
    extensions: [
      StarterKit,
      CustomImage.configure({
        inline: true,
        allowBase64: false,
      }),
    ],
    content: content,
    onUpdate: ({ editor }) => {
      onChange(editor.getHTML());
    },
    editorProps: {
      attributes: {
        class: 'prose prose-sm sm:prose lg:prose-lg xl:prose-2xl mx-auto focus:outline-none',
      },
      // 이미지 클릭 이벤트 처리
      handleClick: (view, pos, event) => {
        const target = event.target as HTMLElement;
        if (target.tagName === 'IMG') {
          const imgElement = target as HTMLImageElement;
          setSelectedImageUrl(imgElement.src);
          setImageWidth(imgElement.width || 500);
          return true;
        }
        setSelectedImageUrl(null);
        return false;
      },
    },
  });

  useEffect(() => {
    if (editor && content !== editor.getHTML()) {
      editor.commands.setContent(content);
    }
  }, [content, editor]);

  // 이미지 업로드 핸들러
  const handleImageUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    if (!file.type.startsWith('image/')) {
      alert('이미지 파일만 업로드 가능합니다.');
      return;
    }

    setIsImageUploading(true);
    try {
      // 이미지 리사이징 (1200px, 80% 품질)
      const resizedBlob = await resizeImage(file, {
        maxWidth: 1200,
        maxHeight: 1200,
        quality: 0.8,
      });

      const resizedFile = blobToFile(resizedBlob, file.name, file.type);
      const result = await uploadImage(resizedFile);
      const url = (result as any).url;

      // 에디터에 이미지 삽입
      if (editor) {
        editor.chain().focus().setImage({ src: url, width: '500' }).run();
      }
    } catch (error) {
      console.error('이미지 업로드 실패:', error);
      alert('이미지 업로드에 실패했습니다.');
    } finally {
      setIsImageUploading(false);
      event.target.value = '';
    }
  };

  // 선택된 이미지 크기 변경
  const handleImageResize = () => {
    if (!editor || !selectedImageUrl) return;

    // 현재 선택된 이미지의 위치 찾기
    const { state } = editor;
    const { doc } = state;
    let imagePos: number | null = null;

    doc.descendants((node, pos) => {
      if (node.type.name === 'image' && node.attrs.src === selectedImageUrl) {
        imagePos = pos;
        return false;
      }
    });

    if (imagePos !== null) {
      editor
      .chain()
      .focus()
      .setNodeSelection(imagePos)
      .updateAttributes('image', { width: `${imageWidth}` })
      .run();
    }

    setSelectedImageUrl(null);
  };

  if (!editor) {
    return null;
  }

  return (
      <div className="tiptap-editor-container">
        {/* 툴바 */}
        <div className="editor-toolbar">
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()}
              className={editor.isActive('heading', { level: 1 }) ? 'is-active' : ''}
          >
            H1
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()}
              className={editor.isActive('heading', { level: 2 }) ? 'is-active' : ''}
          >
            H2
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleHeading({ level: 3 }).run()}
              className={editor.isActive('heading', { level: 3 }) ? 'is-active' : ''}
          >
            H3
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleBold().run()}
              className={editor.isActive('bold') ? 'is-active' : ''}
          >
            Bold
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleItalic().run()}
              className={editor.isActive('italic') ? 'is-active' : ''}
          >
            Italic
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleStrike().run()}
              className={editor.isActive('strike') ? 'is-active' : ''}
          >
            Strike
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleCode().run()}
              className={editor.isActive('code') ? 'is-active' : ''}
          >
            Code
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleBulletList().run()}
              className={editor.isActive('bulletList') ? 'is-active' : ''}
          >
            List
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleOrderedList().run()}
              className={editor.isActive('orderedList') ? 'is-active' : ''}
          >
            Ordered List
          </button>
          <button
              type="button"
              onClick={() => editor.chain().focus().toggleBlockquote().run()}
              className={editor.isActive('blockquote') ? 'is-active' : ''}
          >
            Quote
          </button>

          {/* 이미지 업로드 버튼 */}
          <label className="image-upload-btn">
            <input
                type="file"
                accept="image/*"
                onChange={handleImageUpload}
                disabled={isImageUploading}
                style={{ display: 'none' }}
            />
            <button
                type="button"
                onClick={(e) => {
                  e.preventDefault();
                  (e.currentTarget.previousElementSibling as HTMLInputElement)?.click();
                }}
                disabled={isImageUploading}
                className="toolbar-button"
            >
              {isImageUploading ? '업로드 중...' : 'Image'}
            </button>
          </label>

          <button
              type="button"
              onClick={() => editor.chain().focus().toggleCodeBlock().run()}
              className={editor.isActive('codeBlock') ? 'is-active' : ''}
          >
            Code Block
          </button>
        </div>

        {/* 에디터 */}
        <EditorContent editor={editor} className="editor-content" />

        {/* 이미지 크기 조정 모달 */}
        {selectedImageUrl && (
            <div className="image-resize-modal">
              <div className="modal-content">
                <h3>이미지 크기 조정</h3>
                <img src={selectedImageUrl} alt="Selected" style={{ maxWidth: '100%', marginBottom: '1rem' }} />

                <div className="resize-controls">
                  <label>
                    너비: {imageWidth}px
                    <input
                        type="range"
                        min="200"
                        max="1200"
                        step="50"
                        value={imageWidth}
                        onChange={(e) => setImageWidth(Number(e.target.value))}
                        className="width-slider"
                    />
                  </label>
                </div>

                <div className="modal-buttons">
                  <button onClick={handleImageResize} className="apply-btn">
                    적용
                  </button>
                  <button onClick={() => setSelectedImageUrl(null)} className="cancel-btn">
                    취소
                  </button>
                </div>
              </div>
            </div>
        )}

        <style jsx>{`
        .tiptap-editor-container {
          border: 1px solid #ddd;
          border-radius: 8px;
          overflow: hidden;
        }

        .editor-toolbar {
          display: flex;
          flex-wrap: wrap;
          gap: 0.5rem;
          padding: 1rem;
          background-color: #f5f5f5;
          border-bottom: 1px solid #ddd;
        }

        .editor-toolbar button,
        .toolbar-button {
          padding: 0.5rem 1rem;
          border: 1px solid #ccc;
          background-color: white;
          border-radius: 4px;
          cursor: pointer;
          transition: all 0.2s;
        }

        .editor-toolbar button:hover,
        .toolbar-button:hover {
          background-color: #e8e8e8;
        }

        .editor-toolbar button.is-active {
          background-color: #4CAF50;
          color: white;
          border-color: #4CAF50;
        }

        .editor-toolbar button:disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }

        .editor-content {
          min-height: 400px;
          padding: 1.5rem;
          background-color: white;
        }

        .editor-content :global(img) {
          cursor: pointer;
          border: 2px solid transparent;
          transition: border-color 0.2s;
          max-width: 100%;
          height: auto;
        }

        .editor-content :global(img:hover) {
          border-color: #4CAF50;
        }

        .image-resize-modal {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: rgba(0, 0, 0, 0.7);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1000;
        }

        .modal-content {
          background: white;
          padding: 2rem;
          border-radius: 12px;
          max-width: 600px;
          width: 90%;
          max-height: 90vh;
          overflow-y: auto;
        }

        .modal-content h3 {
          margin: 0 0 1rem 0;
        }

        .resize-controls {
          margin: 1rem 0;
        }

        .resize-controls label {
          display: flex;
          flex-direction: column;
          gap: 0.5rem;
          font-weight: 600;
        }

        .width-slider {
          width: 100%;
          height: 6px;
          border-radius: 3px;
          background: #e0e0e0;
          outline: none;
          cursor: pointer;
        }

        .width-slider::-webkit-slider-thumb {
          -webkit-appearance: none;
          appearance: none;
          width: 18px;
          height: 18px;
          border-radius: 50%;
          background: #4CAF50;
          cursor: pointer;
        }

        .width-slider::-moz-range-thumb {
          width: 18px;
          height: 18px;
          border-radius: 50%;
          background: #4CAF50;
          cursor: pointer;
          border: none;
        }

        .modal-buttons {
          display: flex;
          gap: 1rem;
          margin-top: 1.5rem;
        }

        .apply-btn,
        .cancel-btn {
          flex: 1;
          padding: 0.75rem;
          border: none;
          border-radius: 6px;
          cursor: pointer;
          font-weight: 600;
          transition: opacity 0.2s;
        }

        .apply-btn {
          background-color: #4CAF50;
          color: white;
        }

        .apply-btn:hover {
          opacity: 0.9;
        }

        .cancel-btn {
          background-color: #f44336;
          color: white;
        }

        .cancel-btn:hover {
          opacity: 0.9;
        }
      `}</style>
      </div>
  );
};

export default TiptapEditor